@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);
a.nav-link {
  font-weight: 600;
  text-transform: uppercase !important;
}

.bg-custom {
  background-color: var(--col-blue-brand);
}

.bg-custom-2 {
  background-color: var(--col-blue-brand-light);
}

.border {
  border: 1px solid var(--col-darkest) !important;
}

.btn-nav-link {
  background: none !important;
  font-weight: 300;
}

.hero-bg-img {
  filter: blur(6px);
  height: 100px;
  opacity: 0.5;
  transform: scale(1.2);
  -webkit-filter: blur(6px);
  width: 100%;
  z-index: -10 !important;
}

.hero-links-general-wrapper {
  height: 110px;
  left: 0 !important;
  position: absolute;
  width: 100px;
}

.hero-links-social-wrapper {
  height: 110px;
  position: absolute;
  right: 0;
  width: 100px;
}

.hero-links-wrapper {
  margin-top: 3px;
  z-index: 10;
}

.header-sub-wrapper {
  height: 120px;
  width: 100%;
}

.navbar-brand {
  font-weight: 800;
  text-transform: uppercase !important;
}

.navbar-btn {
  font-weight: 300 !important;
  letter-spacing: -1.5px !important;
}

.navbar-logo-wrapper {
  height: 100px;
  left: 0;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  right: 0;
  text-align: center;
  width: 300px;
  z-index: 10;
}

.nav-btn-active {
  color: var(--col-darkest) !important;
  text-decoration: underline;
}

.vertical-center {
  align-items: center;
  display: flex;
  justify-content: center;
}

/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .navbar-btn {
    height: 55px;
  }
  .navbar-custom {
    padding: 0 !important;
  }
  .navbar-logo-wrapper {
    width: 160px !important;
  }
}

:root {
  --col-blue-brand: #1565c0;
  --col-blue-brand-light: rgb(0, 195, 255);
  --col-red-brand: #c61327;
  --col-darkest: #0f0f0f;
  --col-lightest: #fff;
  --col-lighter: #f5f5f5;
}

* {
  font-family: 'Roboto', sans-serif;
}

.footer {
  background-color: #f5f5f5;
  background-color: var(--col-lighter);
  bottom: 0;
  height: 60px;
  line-height: 60px;
  position: fixed;
  width: 100%;
}

.section-title {
  border-bottom: 1px solid #0f0f0f;
  border-bottom: 1px solid var(--col-darkest);
  display: block;
  font-size: 1.6em;
  font-weight: 800;
  line-height: 1.3;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  position: relative;
}

.section-title:before {
  background: #c61327;
  bottom: 0;
  content: '';
  display: block;
  height: 5px;
  left: 0;
  position: absolute;
  width: 25px;
}

.dropdown-item.active,
.dropdown-item:active {
  background: none !important;
}

a.nav-link:hover {
  text-decoration: underline;
}
a.nav-link:active {
  text-decoration: none;
}

.konami-window {
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 10000 !important;
}

.konami-window img {
  width: 260px;
  z-index: 100000 !important;
}

.vertical-center {
  align-items: center;
  display: flex;
  justify-content: center;
}

